.search {
	background: lighten($brand-success, 55);
	// background: $gray-lighter;
	// background: linear-gradient(rgba(255,255,255,1) 0%, rgba(255,255,255,0) 90%), url("../img/search-bg.jpg") center center no-repeat;
	// background: linear-gradient(rgba(255,255,255,1) 0%, rgba(255,255,255,0) 60%), url("../img/porzsakok-carpet3.jpg") center center no-repeat;
	// background: url("../img/porzsakok-carpet.jpg") center center no-repeat;
	background: linear-gradient(rgba(255,255,255,1) 0%, rgba(255,255,255,0) 60%), url("../img/porzsakok-carpet.jpg") center center no-repeat;
	background-size: cover;
	display: flex;
	flex-direction: column;
	align-content: stretch;
	& > .container {
		display: flex;
		flex-direction: column;
		align-content: space-between;
	}
	@media screen and (orientation:portrait) {
		min-height: 50vh;
	}
	.container {
		padding: 0;
		border-bottom: none;
	}
	.intro {
		padding: 20px 0;
		h1 {
			// color: white;
		}
		p.lead {
			// color: white;
			font-size: 24px;
			strong {
				// color: white;
			}
			@media (max-width: $screen-sm-max) {
				font-size: 18px;
			}
		}
	}
	.tabs {
		text-align: center;
		display: flex;
		flex-grow: 2;
		flex-direction: column;
		align-items: center;
		// align-items: center;
		.tab-content {
			flex-grow: 1;
			flex-wrap: wrap;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			min-height: 220px;
			.tab-pane {
				width: 100%;
			}
			form {
				min-height: 100px;
				padding: 30px 0;
			}
			a.find {
				display: inline-block;
				padding: 10px 20px;
				margin-top: 30px;
				margin-bottom: 10px;
				flex-shrink: 1;
				color: white;
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}
			}
			#search-brand {
				select {
					width: 100%;
					max-width: 600px;
					height: 60px;
					font-size: 18px;
					padding: 0 30px;
					background-color: white;
					background-color: rgba(255,255,255,0.8);
					background-image: url("../img/select-arrow.png");
					background-position: right center;
					background-repeat: no-repeat;
					-webkit-appearance: none;
					-moz-appearance:none;
					appearance: none;
					border: 2px solid white;
					@media (max-width: $screen-sm-max) {
						padding: 0 10px;
						font-size: 14px;
					}
				}
				select::-ms-expand {
					display: none;
				}
			}
			#search-fulltext {
				form {
					padding: 23px 0;
					input {
						width: calc(100% - 60px);
						max-width: 600px;
						height: 60px;
						font-size: 18px;
						padding: 0 30px;
						background: white;
						background: rgba(255,255,255,0.8);
						border: 2px solid white;
						@media (max-width: $screen-sm-max) {
							padding: 0 10px;
							font-size: 14px;
						}
					}
				}
				.btn {
					background: $brand-primary;
					color: white;
					font-size: 36px;
					border-radius: 50%;
					width: 80px;
					height: 80px;
					border: 2px solid white;
					min-width: inherit;
					margin-left: -30px;
					margin-top: -5px;
					box-shadow: 0 0 40px 0 rgba(0,0,0,0.5);
					&:hover {
						box-shadow: 0 0 10px 0 rgba(0,0,0,0.5);
					}
				}
			}
			#search-list {
				// background-color: rgba(255,255,255,0.8);
				width: 100%;
				ul {
					@extend ul.list-links;
					margin-bottom: 0;
					li {
						a {
							font-size: 18px;
							color: white;
							@media (max-width: $screen-sm-max) {
								font-size: 14px;
							}
						}
					}
				}
				a {
					font-size: 14px;
					color: white;
				}
			}
		}
		ul.nav-pills {
			width: 100%;
			// display: inline-block;
			align-self: flex-end;
			li {
				margin-left: 0;
				padding: 0;
				&.active {
					a {
						color: white;
						// background: $brand-success;
						background: none;
						font-weight: 700;
						&:hover {
							background: none;
						}
					}
				}
				a {
					display: flex;
					background: white;
					height: 60px;
					padding: 0 20px;
					color: $gray;
					font-size: 18px;
					align-items: center;
					justify-content: center;
					@media (max-width: $screen-sm-max) {
						font-size: 14px;
						padding: 0 5px;
					}
					&:hover {
						color: white;
						// color: $gray-lighter;
						background: $brand-success;
					}
				}
				@media screen and (max-width: $screen-sm-max) {
					a {
						max-width: 250px;
					}
				}
			}
		}
	}
}
